<script>
  import { onMount } from "svelte";
  import { fly } from "svelte/transition";
  export let id = "";
  export let value = [];
  export let readonly = false;
  export let placeholder = "";

  let input,
    inputValue,
    options = [],
    activeOption,
    showOptions = false,
    selected = {},
    first = true,
    slot;
  const iconClearPath =
    "M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12z";

  onMount(() => {
    slot.querySelectorAll("option").forEach(o => {
      o.selected &&
        value != null &&
        !value.includes(o.value) &&
        (value = [...value, o.value]);
      options = [...options, { value: o.value, name: o.textContent }];
    });
    value &&
      (selected = options.reduce(
        (obj, op) =>
          value.includes(op.value) ? { ...obj, [op.value]: op } : obj,
        {}
      ));
    first = false;
  });

  $: if (!first) value = Object.values(selected).map(o => o.value);
  $: filtered = options.filter(o =>
    inputValue ? o.name.toLowerCase().includes(inputValue.toLowerCase()) : o
  );
  $: if (
    (activeOption && !filtered.includes(activeOption)) ||
    (!activeOption && inputValue)
  )
    activeOption = filtered[0];

  function add(token) {
    if (!readonly) selected[token.value] = token;
  }

  function remove(value) {
    if (!readonly) {
      const { [value]: val, ...rest } = selected;
      selected = rest;
    }
  }

  function optionsVisibility(show) {
    if (readonly) return;
    if (typeof show === "boolean") {
      showOptions = show;
      show && input.focus();
    } else {
      showOptions = !showOptions;
    }
    if (!showOptions) {
      activeOption = undefined;
    }
  }

  function handleKeyup(e) {
    if (e.keyCode === 13) {
      Object.keys(selected).includes(activeOption.value)
        ? remove(activeOption.value)
        : add(activeOption);
      inputValue = "";
    }
    if ([38, 40].includes(e.keyCode)) {
      // up and down arrows
      const increment = e.keyCode === 38 ? -1 : 1;
      const calcIndex = filtered.indexOf(activeOption) + increment;
      activeOption =
        calcIndex < 0
          ? filtered[filtered.length - 1]
          : calcIndex === filtered.length
          ? filtered[0]
          : filtered[calcIndex];
    }
  }

  function handleBlur(e) {
    optionsVisibility(false);
  }

  function handleTokenClick(e) {
    if (e.target.closest(".token-remove")) {
      e.stopPropagation();
      remove(e.target.closest(".token").dataset.id);
    } else {
      optionsVisibility(true);
    }
  }

  function handleOptionMousedown(e) {
    const value = e.target.dataset.value;
    if (selected[value]) {
      remove(value);
    } else {
      add(options.filter(o => o.value === value)[0]);
      input.focus();
    }
  }
</script>

<style lang="scss" src="./css/multiselect.scss">.form-input,
.form-select {
  padding: 0.5rem;
  line-height: 1.5;
  display: block;
  width: 100%;
  border-width: 1px;
  --text-opacity: 1;
  color: #2d3748;
  color: rgba(45, 55, 72, var(--text-opacity));
  --bg-opacity: 1;
  background-color: #fff;
  background-color: rgba(255, 255, 255, var(--bg-opacity));
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  border-radius: 0.25rem;
  text-align: left;
  appearance: none;
  position: relative;
}

.form-input:focus-within, .form-input:focus, .form-input.focus,
  .form-select:focus-within,
  .form-select:focus,
  .form-select.focus {
  --border-opacity: 1;
  border-color: #a0aec0;
  border-color: rgba(160, 174, 192, var(--border-opacity));
  box-shadow: 0 0 0 1px #a0aec0;
}

.form-input::placeholder,
  .form-select::placeholder {
  --text-opacity: 1;
  color: #718096;
  color: rgba(113, 128, 150, var(--text-opacity));
  opacity: 1;
}

.form-select {
  padding-right: 1.5rem;
  background-image: url("data:image/png;base64,");
  background-size: 0.7rem;
  background-repeat: no-repeat;
  background-position: right 0.7rem center;
}

.form-select::-ms-expand {
  opacity: 0;
}

.tokens {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  position: relative;
}

.tokens::after {
  bottom: -1px;
  content: "";
  display: block;
  height: 2px;
  left: 50%;
  position: absolute;
  transition: width 0.3s ease 0s, left 0.3s ease 0s;
  width: 0;
}

.tokens.showOptions::after {
  width: 100%;
  left: 0;
}

.token {
  align-items: center;
  background-color: #e7eaee;
  border-radius: 1.25rem;
  display: flex;
  margin: 0.25rem 0.5rem 0.25rem 0;
  max-height: 1.3rem;
  padding: 0.25rem 0.5rem 0.25rem 0.5rem;
  transition: background-color 0.3s;
  white-space: nowrap;
}

.token:hover {
  background-color: #dce0e5;
}

.readonly .token {
  color: #666666;
}

.token-remove {
  align-items: center;
  background-color: #7b8a9d;
  border-radius: 50%;
  color: #e7eaee;
  display: flex;
  justify-content: center;
  height: 1.25rem;
  margin-left: 0.25rem;
  min-width: 1.25rem;
}

.token-remove:hover {
  background-color: #576a85;
  cursor: pointer;
}

.actions {
  align-items: center;
  display: flex;
  flex: 1;
  min-width: 15rem;
}

input {
  color: transparent;
  text-shadow: 0 0 0 gray;
  text-align: center;
  border: none;
  margin: 0;
  outline: none;
  padding: 0;
  width: 100%;
}

input:focus {
  outline: none;
}

.icon-clear path {
  fill: white;
}

.options {
  left: 0;
  list-style: none;
  margin-block-end: 0;
  margin-block-start: 0;
  max-height: 30vh;
  overflow: auto;
  padding-inline-start: 0;
  position: absolute;
  z-index: 9999;
  top: calc(100% + 1px);
  width: 100%;
}

li {
  background: white;
  cursor: pointer;
  padding: 0.5rem;
}

li:last-child {
  border-bottom-left-radius: 0.2rem;
  border-bottom-right-radius: 0.2rem;
}

li:not(.selected):hover {
  background-color: #e7eaee;
}

li.selected {
  background-color: #66697a;
  color: white;
}

li.selected:nth-child(even) {
  background-color: #66697a;
  color: white;
}


li.selected:hover {
  background-color: #66697a;
}

.hidden {
  display: none;
}</style>

<div class="form-select" class:readonly>
  <div class="tokens" class:showOptions on:click={handleTokenClick}>
    {#each Object.values(selected) as s}
      <div class="token" data-id={s.value}>
        <span>{s.name}</span>
        {#if !readonly}
          <div class="token-remove" title="Remove {s.name}">
            <svg
              class="icon-clear"
              xmlns="http://www.w3.org/2000/svg"
              width="18"
              height="18"
              viewBox="0 0 24 24">
              <path d={iconClearPath} />
            </svg>
          </div>
        {/if}
      </div>
    {/each}
    <div class="actions">
      {#if !readonly}
        <input
          {id}
          readonly
          autocomplete="off"
          bind:value={inputValue}
          bind:this={input}
          on:keyup={handleKeyup}
          on:blur={handleBlur}
          {placeholder} />
      {/if}
    </div>
  </div>

  <select bind:this={slot} type="multiple" class="hidden" multiple>
    <slot />
  </select>

  {#if showOptions}
    <ul
      class="options rounded shadow"
      transition:fly={{ duration: 400, y: 5 }}
      on:mousedown|preventDefault={handleOptionMousedown}>
      {#each filtered as option}
        <li
          class:selected={selected[option.value]}
          class:active={activeOption === option}
          data-value={option.value}>
          {option.name}
        </li>
      {:else}
        <li>No data found.</li>
      {/each}
    </ul>
  {/if}
</div>
